import React, { useState } from 'react';
import MenuHamburger from './MenuHamburger';
import Breadcrumbs from './Breadcrumbs';

// Mock data including Merchant ID 101010 as valid
const mockMerchantData = {
  '123456': { name: 'Merchant A', address: '123 Main St' },
  '654321': { name: 'Merchant B', address: '456 Elm St' },
  '101010': { name: 'Mock Valid Merchant', address: '789 Pine Ave' }, // Hardcoded valid Merchant ID
};

const Adjustments = () => {
  const [merchantId, setMerchantId] = useState('');
  const [merchantDetails, setMerchantDetails] = useState(null);
  const [transactionType, setTransactionType] = useState('');
  const [adjustmentReason, setAdjustmentReason] = useState('');
  const [amount, setAmount] = useState('');
  const [error, setError] = useState('');
  const [isModalOpen, setIsModalOpen] = useState(false);

  const fetchMerchantDetails = (id) => mockMerchantData[id] || null;

  const handleMerchantIDChange = (e) => {
    const id = e.target.value;
    setMerchantId(id);

    const details = fetchMerchantDetails(id);
    if (details) {
      setMerchantDetails(details);
      setError('');
    } else {
      setMerchantDetails(null);
      setError('Merchant ID not found.');
    }
  };

  const validateAmount = (value) => value > 0;

  const handleAmountChange = (e) => {
    const value = e.target.value;
    if (validateAmount(value)) {
      setAmount(value);
      setError('');
    } else {
      setError('Amount must be greater than zero.');
    }
  };

  const handleSubmit = (e) => {
    e.preventDefault();

    if (!merchantDetails) {
      setError('Invalid Merchant ID.');
      return;
    }

    if (!transactionType || !adjustmentReason || !amount) {
      setError('All fields are required.');
      return;
    }

    setIsModalOpen(true);
  };

  const handleConfirm = () => {
    const bankNumber = '1234-5678-9012';
    const processDate = new Date().toISOString().split('T')[0];
    const companyName = merchantDetails.name;

    alert(`Adjustment Successfully Submitted:
      - Merchant ID: ${merchantId}
      - Merchant Name: ${companyName}
      - Transaction Type: ${transactionType}
      - Adjustment Reason: ${adjustmentReason}
      - Amount: $${amount}
      - Bank Number: ${bankNumber}
      - Process Date: ${processDate}
    `);

    setIsModalOpen(false);
    setMerchantId('');
    setMerchantDetails(null);
    setTransactionType('');
    setAdjustmentReason('');
    setAmount('');
  };

  return (
    <div style={{ marginLeft: '60px', padding: '20px', backgroundColor: '#F7F9FC', minHeight: '100vh' }}>
      <MenuHamburger />
      <Breadcrumbs />

      <header style={{ backgroundColor: '#375A7F', padding: '20px', color: 'white', borderRadius: '8px', marginBottom: '20px' }}>
        <h1>Adjustments</h1>
      </header>

      <section style={{ backgroundColor: 'white', padding: '20px', borderRadius: '8px', boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)' }}>
        <h2>Create Adjustments</h2>
        {error && <p style={{ color: 'red', fontWeight: 'bold' }}>{error}</p>}
        <form style={{ display: 'flex', flexDirection: 'column', gap: '15px', maxWidth: '600px' }} onSubmit={handleSubmit}>
          {/* Merchant ID */}
          <div>
            <label htmlFor="merchantId" style={{ display: 'block', fontWeight: 'bold', marginBottom: '5px' }}>
              Merchant ID
            </label>
            <input
              type="text"
              id="merchantId"
              placeholder="Enter Merchant ID"
              value={merchantId}
              onChange={handleMerchantIDChange}
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid #ccc',
                borderRadius: '5px',
              }}
              required
            />
            {merchantDetails && (
              <p style={{ marginTop: '5px', color: 'green' }}>
                <strong>{merchantDetails.name}</strong>, {merchantDetails.address}
              </p>
            )}
          </div>

          {/* Transaction Type */}
          <div>
            <label htmlFor="transactionType" style={{ display: 'block', fontWeight: 'bold', marginBottom: '5px' }}>
              Transaction Type
            </label>
            <select
              id="transactionType"
              value={transactionType}
              onChange={(e) => setTransactionType(e.target.value)}
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid #ccc',
                borderRadius: '5px',
              }}
              required
            >
              <option value="">Select type</option>
              <option value="debit">Debit</option>
              <option value="credit">Credit</option>
            </select>
          </div>

          {/* Adjustment Reason */}
          <div>
            <label htmlFor="adjustmentReason" style={{ display: 'block', fontWeight: 'bold', marginBottom: '5px' }}>
              Adjustment Reason
            </label>
            <select
              id="adjustmentReason"
              value={adjustmentReason}
              onChange={(e) => setAdjustmentReason(e.target.value)}
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid #ccc',
                borderRadius: '5px',
              }}
              required
            >
              <option value="">Select a reason</option>
              <option value="terminal_purchase">Terminal Purchase</option>
              <option value="chargeback_fee">Chargeback Fee</option>
              <option value="risk_adjustment">Risk Adjustment</option>
              <option value="courtesy_adjustment">Courtesy Adjustment</option>
              <option value="billing_correction">Billing Correction</option>
              <option value="fee_reimbursement">Fee Reimbursement</option>
              <option value="other">Other</option>
            </select>
          </div>

          {/* Amount */}
          <div>
            <label htmlFor="amount" style={{ display: 'block', fontWeight: 'bold', marginBottom: '5px' }}>
              Amount
            </label>
            <input
              type="number"
              id="amount"
              placeholder="Enter amount"
              value={amount}
              onChange={handleAmountChange}
              style={{
                width: '100%',
                padding: '10px',
                border: '1px solid #ccc',
                borderRadius: '5px',
              }}
              required
            />
          </div>

          {/* Submit Button */}
          <button
            type="submit"
            style={{
              padding: '10px',
              backgroundColor: '#375A7F',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer',
              fontWeight: 'bold',
            }}
          >
            Submit Adjustment
          </button>
        </form>
      </section>

      {/* Confirmation Modal */}
      {isModalOpen && (
        <div
          style={{
            position: 'fixed',
            top: '0',
            left: '0',
            width: '100%',
            height: '100%',
            backgroundColor: 'rgba(0, 0, 0, 0.5)',
            display: 'flex',
            justifyContent: 'center',
            alignItems: 'center',
          }}
        >
          <div
            style={{
              backgroundColor: 'white',
              padding: '20px',
              borderRadius: '10px',
              maxWidth: '400px',
              textAlign: 'center',
            }}
          >
            <h3>Confirm Adjustment</h3>
            <p><strong>Merchant ID:</strong> {merchantId}</p>
            <p><strong>Transaction Type:</strong> {transactionType}</p>
            <p><strong>Adjustment Reason:</strong> {adjustmentReason}</p>
            <p><strong>Amount:</strong> ${amount}</p>
            <button
              onClick={handleConfirm}
              style={{
                marginTop: '10px',
                padding: '10px',
                backgroundColor: '#375A7F',
                color: 'white',
                border: 'none',
                borderRadius: '5px',
                cursor: 'pointer',
              }}
            >
              Confirm
            </button>
            <button
              onClick={() => setIsModalOpen(false)}
              style={{
                marginTop: '10px',
                padding: '10px',
                backgroundColor: 'red',
                color: 'white',
                border: 'none',
                borderRadius: '5px',
                cursor: 'pointer',
              }}
            >
              Cancel
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Adjustments;
