import React, { useState } from 'react';
import MenuHamburger from './MenuHamburger';
import Breadcrumbs from './Breadcrumbs';
import * as XLSX from 'xlsx';
import JSZip from 'jszip';
import { saveAs } from 'file-saver';

const GeneratedFiles = () => {
  const [files] = useState([
    { date: '2023-12-01', name: 'ACH_2023-12-01.xlsx' },
    { date: '2023-12-02', name: 'ACH_2023-12-02.xlsx' },
    { date: '2023-12-03', name: 'ACH_2023-12-03.xlsx' },
  ]);

  const [searchQuery, setSearchQuery] = useState('');
  const [selectedFiles, setSelectedFiles] = useState([]);
  const [activityLog, setActivityLog] = useState([]);

  const handleDownload = (fileName) => {
    const workbook = XLSX.utils.book_new();
    const worksheet = XLSX.utils.aoa_to_sheet([['Placeholder Data']]);
    XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');
    XLSX.writeFile(workbook, fileName);

    // Add to activity log
    setActivityLog((prevLog) => [...prevLog, { action: 'Download', fileName, date: new Date().toLocaleString() }]);
  };

  const handleBulkDownload = () => {
    const zip = new JSZip();
    selectedFiles.forEach((fileName) => {
      const workbook = XLSX.utils.book_new();
      const worksheet = XLSX.utils.aoa_to_sheet([['Placeholder Data']]);
      XLSX.utils.book_append_sheet(workbook, worksheet, 'Sheet1');
      const fileContent = XLSX.write(workbook, { bookType: 'xlsx', type: 'binary' });
      zip.file(fileName, fileContent, { binary: true });
    });

    zip.generateAsync({ type: 'blob' }).then((content) => {
      saveAs(content, 'generated_files.zip');
    });

    setActivityLog((prevLog) => [
      ...prevLog,
      { action: 'Bulk Download', fileName: selectedFiles.join(', '), date: new Date().toLocaleString() },
    ]);

    setSelectedFiles([]); // Clear selection after download
  };

  const handleFilePreview = (fileName) => {
    alert(`Previewing content for: ${fileName}\n\nPlaceholder Data`);
  };

  const filteredFiles = files.filter((file) =>
    file.name.toLowerCase().includes(searchQuery.toLowerCase())
  );

  const toggleFileSelection = (fileName) => {
    setSelectedFiles((prevSelection) =>
      prevSelection.includes(fileName)
        ? prevSelection.filter((file) => file !== fileName)
        : [...prevSelection, fileName]
    );
  };

  return (
    <div style={{ marginLeft: '60px', padding: '20px', backgroundColor: '#F7F9FC', minHeight: '100vh' }}>
      <MenuHamburger />
      <Breadcrumbs />

      <header style={{ backgroundColor: '#375A7F', padding: '20px', color: 'white', borderRadius: '8px', marginBottom: '20px' }}>
        <h1>Generated Files</h1>
      </header>

      <section style={{ backgroundColor: 'white', padding: '20px', borderRadius: '8px', boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)' }}>
        <h2>List of Generated Files</h2>

        {/* Search Bar */}
        <div style={{ marginBottom: '20px' }}>
          <input
            type="text"
            placeholder="Search files..."
            value={searchQuery}
            onChange={(e) => setSearchQuery(e.target.value)}
            style={{
              width: '100%',
              padding: '10px',
              borderRadius: '5px',
              border: '1px solid #ccc',
            }}
          />
        </div>

        {/* File List */}
        <ul style={{ listStyle: 'none', padding: 0 }}>
          {filteredFiles.map((file, index) => (
            <li key={index} style={{ marginBottom: '10px', display: 'flex', alignItems: 'center', gap: '10px' }}>
              <input
                type="checkbox"
                checked={selectedFiles.includes(file.name)}
                onChange={() => toggleFileSelection(file.name)}
              />
              {file.date} -{' '}
              <button
                onClick={() => handleFilePreview(file.name)}
                style={{
                  background: 'none',
                  border: 'none',
                  color: '#4A90E2',
                  cursor: 'pointer',
                  textDecoration: 'underline',
                }}
              >
                {file.name}
              </button>
              <button
                onClick={() => handleDownload(file.name)}
                style={{
                  marginLeft: '10px',
                  padding: '5px 10px',
                  backgroundColor: '#375A7F',
                  color: 'white',
                  border: 'none',
                  borderRadius: '5px',
                  cursor: 'pointer',
                }}
              >
                Download
              </button>
            </li>
          ))}
        </ul>

        {/* Bulk Download */}
        {selectedFiles.length > 0 && (
          <button
            onClick={handleBulkDownload}
            style={{
              marginTop: '20px',
              padding: '10px',
              backgroundColor: '#52CEE6',
              color: 'white',
              border: 'none',
              borderRadius: '5px',
              cursor: 'pointer',
              fontWeight: 'bold',
            }}
          >
            Download Selected Files ({selectedFiles.length})
          </button>
        )}
      </section>

      {/* Activity Log */}
      <section style={{ marginTop: '20px', backgroundColor: 'white', padding: '20px', borderRadius: '8px', boxShadow: '0 4px 8px rgba(0, 0, 0, 0.1)' }}>
        <h2>Activity Log</h2>
        <ul style={{ listStyle: 'none', padding: 0 }}>
          {activityLog.map((log, index) => (
            <li key={index} style={{ marginBottom: '10px' }}>
              <strong>{log.action}:</strong> {log.fileName} on {log.date}
            </li>
          ))}
        </ul>
      </section>
    </div>
  );
};

export default GeneratedFiles;
