import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import MenuHamburger from './MenuHamburger';

const Rules = () => {
  const navigate = useNavigate();

  // Manual and automatic rules
  const manualRules = [
    { id: 1, type: 'Debit', description: 'Terminal purchase adjustments' },
    { id: 2, type: 'Debit', description: 'Chargeback fees' },
    { id: 3, type: 'Credit', description: 'Courtesy adjustments' },
  ];

  const [automaticRules, setAutomaticRules] = useState([
    { id: 1, name: 'Insufficient Funds', type: 'Debit', trigger: 'Account balance < $0', active: true },
    { id: 2, name: 'High Volume Transactions', type: 'Credit', trigger: 'More than 10 credits in 1 hour', active: false },
  ]);

  const [searchQuery, setSearchQuery] = useState('');
  const [filterType, setFilterType] = useState('');

  const ruleTemplates = [
    { name: 'High-Risk Transactions', type: 'Debit', trigger: 'More than $1000 in a single transaction' },
    { name: 'Frequent Refunds', type: 'Credit', trigger: 'More than 3 refunds in 24 hours' },
  ];

  const toggleRuleStatus = (id) => {
    setAutomaticRules((prevRules) =>
      prevRules.map((rule) =>
        rule.id === id ? { ...rule, active: !rule.active } : rule
      )
    );
  };

  const filteredRules = automaticRules.filter(
    (rule) =>
      rule.name.toLowerCase().includes(searchQuery.toLowerCase()) &&
      (filterType ? rule.type === filterType : true)
  );

  const applyTemplate = (template) => {
    navigate('/create-rule', { state: template });
  };

  return (
    <div style={{ backgroundColor: '#F7F9FC', minHeight: '100vh', padding: '20px' }}>
      <MenuHamburger />
      <header style={{ marginLeft: '60px', backgroundColor: '#375A7F', color: 'white', padding: '20px', borderRadius: '8px' }}>
        <h1>Rules</h1>
      </header>
      <div style={{ marginTop: '20px', marginLeft: '60px' }}>
        {/* Manual Rules */}
        <h2>Manual Rules</h2>
        <ul>
          {manualRules.map((rule) => (
            <li key={rule.id}>
              <strong>{rule.type}:</strong> {rule.description}
            </li>
          ))}
        </ul>

        {/* Filters for Automatic Rules */}
        <div style={{ marginTop: '20px', display: 'flex', gap: '20px', flexWrap: 'wrap' }}>
          <div>
            <label htmlFor="search" style={{ fontWeight: 'bold', marginRight: '10px' }}>Search by Name:</label>
            <input
              id="search"
              type="text"
              value={searchQuery}
              onChange={(e) => setSearchQuery(e.target.value)}
              placeholder="Enter rule name"
              style={{
                padding: '10px',
                borderRadius: '5px',
                border: '1px solid #ccc',
              }}
            />
          </div>
          <div>
            <label htmlFor="typeFilter" style={{ fontWeight: 'bold', marginRight: '10px' }}>Filter by Type:</label>
            <select
              id="typeFilter"
              value={filterType}
              onChange={(e) => setFilterType(e.target.value)}
              style={{
                padding: '10px',
                borderRadius: '5px',
                border: '1px solid #ccc',
              }}
            >
              <option value="">All</option>
              <option value="Debit">Debit</option>
              <option value="Credit">Credit</option>
            </select>
          </div>
        </div>

        {/* Automatic Rules */}
        <h2 style={{ marginTop: '20px' }}>Automatic Rules</h2>
        <table style={{ width: '100%', marginTop: '10px', borderCollapse: 'collapse' }}>
          <thead>
            <tr style={{ backgroundColor: '#375A7F', color: 'white' }}>
              <th>Name</th>
              <th>Type</th>
              <th>Trigger</th>
              <th>Status</th>
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            {filteredRules.map((rule) => (
              <tr key={rule.id} style={{ backgroundColor: 'white', borderBottom: '1px solid #ddd' }}>
                <td>{rule.name}</td>
                <td>{rule.type}</td>
                <td>{rule.trigger}</td>
                <td>{rule.active ? 'Active' : 'Inactive'}</td>
                <td>
                  <button
                    onClick={() => toggleRuleStatus(rule.id)}
                    style={{
                      padding: '5px 10px',
                      backgroundColor: rule.active ? 'red' : '#4A90E2',
                      color: 'white',
                      border: 'none',
                      borderRadius: '5px',
                      cursor: 'pointer',
                      marginRight: '10px',
                    }}
                  >
                    {rule.active ? 'Deactivate' : 'Activate'}
                  </button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>

        {/* Rule Templates */}
        <h2 style={{ marginTop: '20px' }}>Rule Templates</h2>
        <div style={{ display: 'flex', gap: '20px', marginTop: '10px' }}>
          {ruleTemplates.map((template, index) => (
            <button
              key={index}
              onClick={() => applyTemplate(template)}
              style={{
                padding: '10px',
                backgroundColor: '#52CEE6',
                color: 'white',
                border: 'none',
                borderRadius: '5px',
                cursor: 'pointer',
                fontWeight: 'bold',
              }}
            >
              Apply Template: {template.name}
            </button>
          ))}
        </div>

        {/* Create Rule Button */}
        <button
          onClick={() => navigate('/create-rule')}
          style={{
            backgroundColor: '#4A90E2',
            padding: '10px',
            border: 'none',
            borderRadius: '5px',
            color: 'white',
            cursor: 'pointer',
            marginTop: '20px',
          }}
        >
          Create Rule
        </button>
      </div>
    </div>
  );
};

export default Rules;
